import {Component} from '@angular/core';
import {Empresa} from '../models/Empresa';
import {UserService} from '../services/user.service';

@Component({
  selector: 'add-empresa',
  templateUrl: './add-empresa.component.html'
})
export class AddEmpresa {
  newEmpresa: Empresa = new Empresa();
  registered: boolean = false;

  constructor (private userService: UserService) {}

  onSubmit() {
    this.userService.sendEmpresa(this.newEmpresa)
    .subscribe(
      data => {
        this.newEmpresa = new Empresa();
        let empresaReturn: Empresa;
        

        try {
          empresaReturn = JSON.parse(JSON.parse(JSON.stringify(data))._body)
          this.registered = true;

        }
        catch(err) {
          alert("Nome Fantasia j utilizado. Tente outro.")
        }
      
      },
      error => console.log(error)
      );
  }
}



