import {Component, ElementRef} from '@angular/core';
import {User} from '../models/User';
import {Empresa} from '../models/Empresa';
import {RegisterService} from '../services/register.service';
import {LoginService} from '../services/login.service';
import {UserService} from '../services/user.service';
import {ModalComponent}  from '../components/modal-pdf.component';
import { ViewChild } from "@angular/core/src/metadata/di";



@Component({
  selector: 'register',
  templateUrl: './register.component.html'
})
export class Register {
  newUser: User = new User();
  registered: boolean = false;
  empresas: Empresa[];
  newEmpresa: Empresa = new Empresa();

  @ViewChild("cancelar") cancelarref:ElementRef;


  constructor (private registerService: RegisterService, private loginService: LoginService, 
    private userService: UserService) {

    this.refreshEmpresas();


  }


  refreshEmpresas(){
    this.loginService.sendToken(localStorage.getItem("token")).subscribe(
        data => {
          this.userService.getAllEmpresas().subscribe(
            data => console.log(this.empresas = JSON.parse(JSON.parse(JSON.stringify(data))._body)),
            error => console.log(error)
            );
        },
        error => console.log(error)
        );

        console.log(this.empresas);

  }

  onSubmit() {
    this.registerService.sendUser(this.newUser)
    .subscribe(
      data => {
        this.newUser = new User();
        let userReturn: User;
        

        try {
          userReturn = JSON.parse(JSON.parse(JSON.stringify(data))._body)
          this.registered = true;

        }
        catch(err) {
          alert("Login j utilizado. Tente outro.")
        }
      
      },
      error => console.log(error)
      );
  }


  salvarEmpresa() {
    this.userService.sendEmpresa(this.newEmpresa)
    .subscribe(
      data => {
        this.newEmpresa = new Empresa();
        let empresaReturn: Empresa;
        

        try {
          empresaReturn = JSON.parse(JSON.parse(JSON.stringify(data))._body)
          this.refreshEmpresas();
          this.cancelarref.nativeElement.click();

        }
        catch(err) {
          alert("Nome Fantasia j utilizado. Tente outro.")
        }
      
      },
      error => console.log(error)
      );
  }

  checkBoxAdmin(){
      this.newUser.isAdmin = !this.newUser.isAdmin;
  }


}



