import { Component, ElementRef, Renderer } from '@angular/core';
import {BrowserModule, DomSanitizer, SafeResourceUrl} from '@angular/platform-browser'
import {Pdf} from '../models/pdf';

import {User} from '../models/user';
import {PdfUserFilter} from '../models/filters/pdf-user-filter';
import {Router} from '@angular/router';
import {ViewChild} from "@angular/core/src/metadata/di";

import {PdfService} from '../services/pdf.service';
import {UserService} from '../services/user.service';
import {HostService}  from '../services/host.service';
import {UploadPdfService} from '../services/upload-pdf.service';
import {AddPdfService} from '../services/add-pdf.service';

import {ModalComponent}  from '../components/modal-pdf.component';
import {ModalLgComponent}  from '../components/modal-pdf-lg.component';



@Component({
  selector: 'pdf-user',
  templateUrl: './pdf-user.component.html'
})

export class PdfUser{

  @ViewChild("nao") naodelref:ElementRef;
  @ViewChild("modal2") modal:ModalComponent;
  @ViewChild("modalNova") modalNova:ModalLgComponent;
  @ViewChild("modalEditar") modalEditar:ModalLgComponent;
  @ViewChild("fileInput") fileInput:ElementRef;
  @ViewChild("fileInputModal") fileInputModal:ElementRef;
  

  pdfSrc = 'https://vadimdez.github.io/ng2-pdf-viewer/pdf-test.pdf';
  pdfSrcBase = this.hostService.host+'pdf/download'
  pdfSrcBase2 = this.hostService.host+'norest/pdf/download/'
  

  page:number = 1;
  pageurl:SafeResourceUrl;
  pageurl2:SafeResourceUrl;

  urlPdfs:SafeResourceUrl[];

  pdfs: Pdf[];
  selectedPdf: Pdf;
  pdfUserFilter: PdfUserFilter = new PdfUserFilter();
  zoom=0.5;

  subtipoRender: boolean = false;
  refreshAll: boolean = false;

  aindaNaoBuscado: boolean;

  private user: User;

  newPdf: Pdf = new Pdf();
  pdfAdded: boolean = false;
  userModal: User;
  subtipoRenderModal: boolean = false;

  arquivoAntigoNomeModalEditar: string = '';
  arquivoNovoNomeModalEditar: string = '';



  constructor(private domSanitizer:DomSanitizer, private pdfService: PdfService,  private router: Router, 
    private userService: UserService, private rd: Renderer, private hostService: HostService,
    private uploadPdfService: UploadPdfService, private addPdfService: AddPdfService) {

   /* this.userService.getUserByName(localStorage.getItem("currentUserName")).subscribe(
      user => {
        this.user = JSON.parse(JSON.parse(JSON.stringify(user))._body);
        console.log(this.user);
        this.pdfService.getPdfsByUser(this.user).subscribe(
          pdf => {console.log(this.pdfs = JSON.parse(JSON.parse(JSON.stringify(user))._body).pdfList);
          },
          error => console.log(error)
          );
      },
      error => console.log(error)
      );*/

      this.aindaNaoBuscado =true;

    }

    editarPdf(pdf: Pdf){
      this.uploadPdfService.fileIsInside = false;
      this.uploadPdfService.fileIsUploaded = false;
      this.pdfAdded = false;
      this.newPdf = pdf;
      this.arquivoAntigoNomeModalEditar = pdf.pdfName;
      this.arquivoNovoNomeModalEditar = "";
      this.cleanModalEditar();
      this.onChangeDropModal();
      this.modalEditar.show();
      }

    fecharModalEditar(){
      if(this.refreshAll){
        this.refresh();} else{this.findPdfs();}
      this.modalEditar.hide();
    }

    editarPdfModalFileChange(evento: any){
       this.arquivoNovoNomeModalEditar = this.uploadPdfService.fileChangeEventModal(evento);
       }

    deletePdf(pdf){

      this.pdfService.deletePdf(pdf).subscribe(
        username => {this.userService.getUserByName(localStorage.getItem("currentUserName")).subscribe(
          user => {
            this.user = JSON.parse(JSON.parse(JSON.stringify(user))._body);
            console.log(this.user);
            this.pdfService.getPdfsByUser(this.user).subscribe(
              pdf => {console.log(this.pdfs = JSON.parse(JSON.parse(JSON.stringify(user))._body).pdfList);
              },
              error => console.log(error)
              );
          },
          error => console.log(error)
          );
      });


      this.naodelref.nativeElement.click();
      if(this.refreshAll){
        this.refresh();} else{this.findPdfs();}
      }

      refresh(){
        this.userService.getUserByName(localStorage.getItem("currentUserName")).subscribe(
          user => {
            this.user = JSON.parse(JSON.parse(JSON.stringify(user))._body);
            this.pdfService.getPdfsByUser(this.user).subscribe(
              pdf => {this.pdfs = JSON.parse(JSON.parse(JSON.stringify(user))._body).pdfList;
                this.refreshAll = true;
                this.aindaNaoBuscado =false;
              },
              error => console.log(error)
              );
          },
          error => console.log(error)
          );
      }


      findPdfs(){
        this.userService.getUserByName(localStorage.getItem("currentUserName")).subscribe(
          user => {
            this.user = JSON.parse(JSON.parse(JSON.stringify(user))._body);
            this.pdfUserFilter.user = this.user;
            this.pdfService.getPdfsByFilter(this.pdfUserFilter).subscribe(
              pdfs => {this.pdfs = JSON.parse(JSON.parse(JSON.stringify(pdfs))._body).pdfList;
                this.refreshAll = false;
                this.aindaNaoBuscado =false;
              },
              error => console.log(error)
              );
          },
          error => console.log(error)
          );
      }


      getPdf(pdf: Pdf){
        this.userService.getUserByName(localStorage.getItem("currentUserName")).subscribe(
          user => {
            this.user = JSON.parse(JSON.parse(JSON.stringify(user))._body);
            console.log(this.user);
            this.pdfService.downloadPdfPost(pdf).subscribe(
              (res) => {
                /*let blob: Blob = new Blob([res.arrayBuffer()], {type: 'application/pdf'});*/
                var pdfResponse = new Blob([res.blob()], { type: 'application/pdf' });
                console.log(pdfResponse.size + " bytes file downloaded. File type: ", pdfResponse.type);                

                var fileURL = URL.createObjectURL(pdfResponse);
                document.getElementById("pdfModal").setAttribute("src", fileURL);
                this.modal.show();


                /* window.open(fileURL);*/
              },
              error => console.log(error)
              );
          },
          error => console.log(error)
          );
      }

      filtersSetted(){
        if(this.pdfUserFilter.pdf.ano  && this.pdfUserFilter.pdf.tipo){

          if(this.pdfUserFilter.pdf.tipo == 1){
            if(this.pdfUserFilter.pdf.subTipo){
              return true;
            } 
            else { 
              return false;
            }
          } 
          else { 
            return true;
          } 

        } 
      }

      openInNewTab(url) {
        var a = document.createElement("a");
        a.target = "_blank";
        a.href = url;
        a.click();
      }

      existePdf(){
        if(this.pdfs == null || this.pdfs.length == 0){
          return true;
        } else {
          return false;
        }
      }

      existePdfAndAindaNaoBuscado(){
        if(this.existePdf() && this.aindaNaoBuscado)
          return true;
        else return false;
      }

      existePdfAndNotAindaNaoBuscado(){
        if(this.existePdf() && !this.aindaNaoBuscado)
          return true;
        else return false;
      }


      onChangeDrop(teste){
        if(this.pdfUserFilter.pdf.tipo == 1){
          this.subtipoRender = true;
        } else {
          this.pdfUserFilter.pdf.subTipo = null;
          this.subtipoRender = false;

        }

      }

      saveNewInspecao() {

        this.newPdf.pdfName = this.uploadPdfService.pdfName;

        this.userService.getUserByName(localStorage.getItem("currentUserName")).subscribe(
          user => {
            this.userModal = JSON.parse(JSON.parse(JSON.stringify(user))._body);
            this.newPdf.user = this.userModal;
            this.addPdfService.sendPdf(this.newPdf)
            .subscribe(
              data => {

                let pdfReturn: Pdf;

                try {
                  pdfReturn = JSON.parse(JSON.parse(JSON.stringify(data))._body)
                  if(this.uploadPdfService.fileIsInside){
                  this.uploadPdfService.upload();
                  }
                  this.pdfAdded = true;
                  this.newPdf = new Pdf();
                  this.modalNovaClose();

                }
                catch(err) {
                  alert("J existe uma inpeo com esse ttulo. Tente outro.")
                }

              },
              error => console.log(error)
              );
          },
          error => console.log(error)
          )
      }

      saveEdicao() {
        this.newPdf.pdfName = this.arquivoNovoNomeModalEditar;
        this.userService.getUserByName(localStorage.getItem("currentUserName")).subscribe(
          user => {
            this.userModal = JSON.parse(JSON.parse(JSON.stringify(user))._body);
            this.newPdf.user = this.userModal;
            this.pdfService.updatePdf(this.newPdf)
            .subscribe(
              data => {

                let pdfReturn: Pdf;

                try {
                  pdfReturn = JSON.parse(JSON.parse(JSON.stringify(data))._body)
                  if(this.uploadPdfService.fileIsInside)
                  this.uploadPdfService.upload();
                  this.pdfAdded = true;
                  this.newPdf = new Pdf();
                  this.fecharModalEditar();
                }
                catch(err) {
                  alert("J existe uma inpeo com esse ttulo. Tente outro.")
                }

              },
              error => console.log(error)
              );
          },
          error => console.log(error)
          )

      }

      onChangeDropModal(){
        if(this.newPdf.tipo == 1){
          this.subtipoRenderModal = true;
        } else {
          this.newPdf.subTipo = null;
          this.subtipoRenderModal = false;

        }

      }

      ngOnInit() {
        this.uploadPdfService.fileIsInside = false;
        this.uploadPdfService.fileIsUploaded = false; 
      }

      modalNovaOpen(){
        this.uploadPdfService.fileIsInside = false;
        this.uploadPdfService.fileIsUploaded = false;
        this.pdfAdded = false;
        this.newPdf = new Pdf();
        this.cleanFilesModalNova();
        this.modalNova.show();
      }

      modalNovaClose(){
      if(this.refreshAll){
        this.refresh();} else{this.findPdfs();}
      this.modalNova.hide();
    }

      cleanFilesModalNova(){
        this.rd.setElementProperty(this.fileInput.nativeElement, 'value', '');
      }

      cleanModalEditar(){
        this.rd.setElementProperty(this.fileInputModal.nativeElement, 'value', '');
      }

}


