import {Component} from '@angular/core';
import {Teste} from '../models/teste';

import {TestarService} from '../services/testar.service';



@Component({
  selector: 'testar',
  templateUrl: './testar.component.html'
})
export class Testar {
  newTeste: Teste = new Teste();
  testado: boolean = false;


constructor (private testarService: TestarService) {}
 

  onSubmit() {
    console.log("submit test");
    this.testarService.sendTeste(this.newTeste)
    .subscribe(
      data => {
        this.newTeste = new Teste();
        this.testado = true;
      },
      error => console.log(error)
    );
  }
}



