import {Component, ElementRef} from '@angular/core';
import {LoginService} from '../services/login.service';
import {PdfService} from '../services/pdf.service';
import {UserService} from '../services/user.service';
import { ViewChild } from "@angular/core/src/metadata/di";

import { ModalComponent }  from '../components/modal-pdf.component';
import { Router } from '@angular/router';

import { User } from '../models/user';
import { NovaSenhaCampos } from '../models/novaSenhaCampos';

@Component({
  selector: 'nav-bar',
  templateUrl:'./nav-bar.component.html'
})

export class NavBar{

  @ViewChild("nao") naodelref: ElementRef;
  @ViewChild("modalSenha") modalSenha: ModalComponent;
  @ViewChild("modalConfirmarSenha") modalConfirmarSenha: ModalComponent;

  private model = {'username':'', 'password':'', 'isAdmin':''};
  myLocalStorage;
  novaSenhaCampos: NovaSenhaCampos = new NovaSenhaCampos();
  passVerify: string;

  constructor (private loginService:LoginService, private pdfService: PdfService, private userService: UserService, 
    private router: Router) {
    this.myLocalStorage=localStorage;
    this.novaSenhaCampos.userName = localStorage.getItem("currentUserName");
  }

  onClick() {
    if (this.loginService.checkLogin()) {
      this.loginService.logout();
      this.naodelref.nativeElement.click();
      this.router.navigate(['/login'],
        {queryParams: {}});
    }
  }

  modalSenhaOpen(){
        this.novaSenhaCampos = new NovaSenhaCampos();
        this.novaSenhaCampos.userName = localStorage.getItem("currentUserName");
        this.passVerify = '';
        this.modalSenha.show();
  }

  modalConfirmaSenhaOpen(){
    this.modalConfirmarSenha.show();
  }

  salvarNovaSenha(){
    if(this.verificarSenhas(this.novaSenhaCampos.newPassword)){
      this.modalConfirmaSenhaOpen();
    }
  }

  confirmarNovaSenha(){
    this.userService.updateSenha(this.novaSenhaCampos).subscribe(
      data => {
        this.modalConfirmarSenha.hide();
        this.modalSenha.hide();}
      , error => console.log(error)
      );
  }

  verificarSenhas(pass:string){
    if (pass.length < 6) {
        alert("A senha deve conter pelo menos 6 caracteres.");
        return false;
    } else if (pass.length > 20) {
        alert("A senha no pode ter mais do que 20 caracteres");
        return false;
    } else if (pass.search(/\d/) == -1) {
        alert("A senha deve conter pelo menos um nmero.");
        return false;
    } else if (pass.search(/[a-zA-Z]/) == -1) {
        alert("A senha deve conter pelo menos uma letra");
        return false;
    } else if (pass.search(/[`~!@#$%^&*()_|+\-=?;:'",.<>\{\}\[\]\\\/]/gi) != -1) {
        alert("A senha no pode conter caracteres especiais");
        return false;
    }
    else if(!(this.passVerify === pass)){
            alert("As duas senhas devem coincidir!");
          return false;
        } else return true;
  }

}




