import { ModuleWithProviders }  from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from './guards/auth.guard';
import { AuthGuardAdmin } from './guards/auth.guard.admin';

import { HomeComponent }  from './components/home.component';
import { Register }  from './components/register.component';
import { Testar }  from './components/testar.component';
import { Login }  from './components/login.component';
import { MyAlbum }  from './components/my-album.component';
import { AddPhoto }  from './components/add-photo.component';
import { PdfUser } from './components/pdf-user.component';
import { AddPdf } from './components/add-pdf.component';
import { PdfAdmin } from './components/pdf-admin.component';
import { AddEmpresa } from './components/add-empresa.component';
import { UserAdmin } from './components/user-admin.component';





const appRoutes: Routes = [
  {
    path: '',
    redirectTo: '/pdf-user',
    pathMatch: 'full',
    canActivate: [AuthGuard]
  },
  {
    path: 'register',
    component: Register,
    canActivate: [AuthGuardAdmin]
  },
  {
    path: 'testar',
    component: Testar,
    canActivate: [AuthGuard]
  },
  {
    path: 'login',
    component: Login
  }, 
  {
    path: 'pdf-user',
    component: PdfUser,
    canActivate: [AuthGuard]
  },
  {
    path: 'add-pdf',
    component: AddPdf,
    canActivate: [AuthGuard]
  },
  
  {
    path: 'pdf-admin',
    component: PdfAdmin,
    canActivate: [AuthGuardAdmin]
  },
  {
    path: 'add-empresa',
    component: AddEmpresa,
    canActivate: [AuthGuardAdmin]
  },
  {
    path: 'user-admin',
    component: UserAdmin,
    canActivate: [AuthGuardAdmin]
  }


  




  
  
];

export const routing: ModuleWithProviders = RouterModule.forRoot(appRoutes);


