import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { routing } from './app.routing';
import { HomeComponent } from './components/home.component';
import { PhotoList } from './components/photo-list.component';
import { NavBar } from './components/nav-bar.component';
import { Register } from './components/register.component';
import { Testar } from './components/testar.component';
import { Login } from './components/login.component';
import { MyAlbum } from './components/my-album.component';
import { AddPhoto } from './components/add-photo.component';
import { PdfUser } from './components/pdf-user.component';
import { PdfAdmin } from './components/pdf-admin.component';
import { UserAdmin } from './components/user-admin.component';
import { AddPdf } from './components/add-pdf.component';
import { AddEmpresa } from './components/add-empresa.component';

import { MyModal }   from './components/modal.component';
import {ModalComponent} from './components/modal-pdf.component';
import {ModalLgComponent} from './components/modal-pdf-lg.component';


import { PdfViewerComponent } from 'ng2-pdf-viewer';

import { AuthGuard } from './guards/auth.guard';
import { AuthGuardAdmin } from './guards/auth.guard.admin';
import { AuthGuardSuperAdmin } from './guards/auth.guard.superadmin';

import { PhotoService } from './services/photo.service';    
import { RegisterService } from './services/register.service';    
import { TestarService } from './services/testar.service';  
import { LoginService } from './services/login.service';  
import { UploadPhotoService } from './services/upload-photo.service';  
import { AddPhotoService } from './services/add-photo.service';  
import { UserService } from './services/user.service';  
import { AddPdfService } from './services/add-pdf.service';  
import { UploadPdfService } from './services/upload-pdf.service';  
import { PdfService } from './services/pdf.service';  
import { HostService }  from './services/host.service';

import { ModalModule } from 'angular2-modal';
import { BootstrapModalModule } from 'angular2-modal/plugins/bootstrap';


@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    PhotoList,
    NavBar,
    Register,
    Testar,
    Login,
    MyAlbum, 
    AddPhoto,
    PdfUser,
    AddPdf,
    PdfAdmin,
    UserAdmin,
    MyModal,
    ModalComponent,
    ModalLgComponent,
    PdfViewerComponent,
    AddEmpresa
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule, 
    routing,
    ModalModule.forRoot(),
    BootstrapModalModule
  ],
  providers: [
    PhotoService, 
    RegisterService, 
    TestarService, 
    LoginService, 
    UploadPhotoService, 
    AddPhotoService,
    AddPdfService,
    UploadPdfService, 
    UserService,
    PdfService,
    HostService,
    AuthGuard,
    AuthGuardAdmin,
    AuthGuardSuperAdmin],
  bootstrap: [AppComponent]
})
export class AppModule { }



