import {Injectable} from '@angular/core';
import {Pdf} from '../models/pdf';
import {Http, Headers, RequestOptions, RequestMethod, ResponseContentType} from '@angular/http';
import {User} from '../models/user';
import {PdfUserFilter} from '../models/filters/pdf-user-filter';

import {HostService}  from '../services/host.service';


@Injectable()
export class PdfService {
 
  constructor (private http:Http, private hostService:HostService){}

  getPdfs() {
    let url = this.hostService.host+"rest/super/pdf/allPdfs";
    return this.http.get(url);
  }

  getAllPdfs() {
    let tokenUrl1 = this.hostService.host+"rest/super/pdf/getAllPdfs";
    let headers1 = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(tokenUrl1, JSON.stringify(""), {headers: headers1});
  }

  getPdfById (pdfId: number) {
    let tokenUrl1 = this.hostService.host+"rest/simple/pdf/pdfId";
    let headers1 = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(tokenUrl1, JSON.stringify(pdfId), {headers: headers1});
  }

  downloadPdfPost (pdf: Pdf) {
    let tokenUrl1 = this.hostService.host+"rest/simple/pdf/download/"+localStorage.getItem("currentUserName");
    let headers1 = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(tokenUrl1, JSON.stringify(pdf), {method: RequestMethod.Post,
        responseType: ResponseContentType.ArrayBuffer, headers: headers1});
  }

   getPdfsByUser (user: User) {
    let tokenUrl1 = this.hostService.host+"rest/simple/pdf/user";
    let headers1 = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(tokenUrl1, JSON.stringify(user), {headers: headers1});
  }

  getPdfsByEmpresaFantasyName (emprenaFantasyName: string) {
    let tokenUrl1 = this.hostService.host+"rest/admin/pdf/pdfsByEmpresaFantasyName";
    let headers1 = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(tokenUrl1, JSON.stringify(emprenaFantasyName), {headers: headers1});
  }

  getPdfsByFilter (pdfUserFilter: PdfUserFilter) {
    let tokenUrl1 = this.hostService.host+"rest/simple/pdf/filter";
    let headers1 = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(tokenUrl1, JSON.stringify(pdfUserFilter), {headers: headers1});
  }

  updatePdf(pdf: Pdf) {
    let tokenUrl1 = this.hostService.host+"rest/simple/pdf/update";
    let headers1 = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(tokenUrl1, JSON.stringify(pdf), {headers: headers1});
  }

   deletePdf(pdf: Pdf) {
    let tokenUrl1 = this.hostService.host+"rest/simple/pdf/delete";
    let headers1 = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(tokenUrl1, JSON.stringify(pdf), {headers: headers1});
  }


}



