import { Component, ElementRef, Renderer, OnInit } from '@angular/core';
import {BrowserModule, DomSanitizer, SafeResourceUrl} from '@angular/platform-browser';
import {Pdf} from '../models/pdf';
import {PdfService} from '../services/pdf.service';
import {UserService} from '../services/user.service';
import {LoginService} from '../services/login.service';
import {PdfUserFilter} from '../models/filters/pdf-user-filter';

import {User} from '../models/user';
import {Empresa} from '../models/empresa';
import {NovaSenhaCampos} from '../models/novaSenhaCampos';
import {Router} from '@angular/router';
import { ViewChild } from "@angular/core/src/metadata/di";
import {HostService} from '../services/host.service';
import {RegisterService} from '../services/register.service';
import {AuthGuardSuperAdmin} from '../guards/auth.guard.superadmin';

import {ModalComponent}  from '../components/modal-pdf.component';



@Component({
  selector: 'user-admin',
  templateUrl: './user-admin.component.html'
})

export class UserAdmin implements OnInit {

  @ViewChild("nao") naodelref:ElementRef;
  @ViewChild("cancelarEmpresa") cancelarEmpresaRef:ElementRef;
  @ViewChild("modalBloquear") modalBloquear:ModalComponent;
  @ViewChild("modalNovo") modalNovo:ModalComponent;
  @ViewChild("modalEditar") modalEditar:ModalComponent;
  


  pdfSrcBase = this.hostService.host+'/pdf/download'

  page:number = 1;
  pageurl:SafeResourceUrl;
  pageurl2:SafeResourceUrl;

  urlPdfs:SafeResourceUrl[];

  pdfs: Pdf[];
  selectedPdf: Pdf;
  zoom=0.5;

  pdfUserFilter: PdfUserFilter = new PdfUserFilter();

  users: User[];
  user: User;
  newUser: User = new User();
  passVerify: string;
  userEdit: User = new User();
  empresas: Empresa[];
  newEmpresa: Empresa = new Empresa();

  userFilter: string;
  empresaFilter: Empresa;

  subtipoRender: boolean = false;

  aindaNaoBuscado: boolean;



  constructor(private domSanitizer:DomSanitizer, private pdfService: PdfService,  private router: Router, 
    private userService: UserService, private rd: Renderer, private loginService: LoginService, 
    private hostService: HostService, private registerService: RegisterService, private authGuardSuperAdmin: AuthGuardSuperAdmin) {

  }

  ngOnInit(){
    this.userService.getUserByName(localStorage.getItem("currentUserName")).subscribe(
      user => {
        this.loginService.userLogado = JSON.parse(JSON.parse(JSON.stringify(user))._body);

        if(this.authGuardSuperAdmin.canActivate()){
          this.userService.getAllUsers().subscribe(
            data => {this.users = JSON.parse(JSON.parse(JSON.stringify(data))._body);
              this.refreshEmpresas();},
              error => console.log(error)
              );
        }

        else {
          this.userService.getUsersByEmpresa(localStorage.getItem("currentEmpresa")).subscribe(
            data => this.users = JSON.parse(JSON.parse(JSON.stringify(data))._body),
            error => console.log(error)
            );
        }

      }, error => console.log(error)
      );  



  }

  findUsers(){

    if(this.authGuardSuperAdmin.canActivate()) {
      if(this.userFilter && this.empresaFilter){

        this.userService.getUsersByUserNameAndEmpresaName(this.userFilter+"/"+this.empresaFilter.empresaName).subscribe(
          data => this.users = JSON.parse(JSON.parse(JSON.stringify(data))._body),
          error => console.log(error)
          );

      } else if(this.userFilter){

        this.userService.getUsersByUserName(this.userFilter).subscribe(
          data => this.users = JSON.parse(JSON.parse(JSON.stringify(data))._body),
          error => console.log(error)
          );

      } else if(this.empresaFilter){

        this.userService.getUsersByEmpresa(this.empresaFilter.empresaName).subscribe(
          data => this.users = JSON.parse(JSON.parse(JSON.stringify(data))._body),
          error => console.log(error)
          );

      } else {

        this.userService.getAllUsers().subscribe(
          data => this.users = JSON.parse(JSON.parse(JSON.stringify(data))._body),
          error => console.log(error)
          );

      }
    }
    else {
      if(this.userFilter) {
        this.userService.getUsersByUserNameAndEmpresaName(this.userFilter+"/"+localStorage.getItem("currentEmpresa")).subscribe(
          data => console.log(this.users = JSON.parse(JSON.parse(JSON.stringify(data))._body)),
          error => console.log(error)
          );
      }
      else {
        this.userService.getUsersByEmpresa(localStorage.getItem("currentEmpresa")).subscribe(
          data => this.users = JSON.parse(JSON.parse(JSON.stringify(data))._body),
          error => console.log(error)
          );
      }
    }
  }

  modalNovoOpen(){
    this.newUser = new User();
    this.passVerify = '';
    this.modalNovo.show();
  }

  modalNovoClose(){
    this.findUsers();
    this.modalNovo.hide();
  }


  refreshEmpresas(){
    this.userService.getAllEmpresas().subscribe(
      data => this.empresas = JSON.parse(JSON.parse(JSON.stringify(data))._body),
      error => console.log(error)
      );
  }

  saveNewUser(pass:string) {
    if(this.verificarLogin(this.newUser.userName))
    if(this.verificarSenhas(pass)){

      if(this.authGuardSuperAdmin.canActivate())

        this.registerService.sendUser(this.newUser)
      .subscribe(
        data => {
          this.newUser = new User();
          let userReturn: User;

          try {
            userReturn = JSON.parse(JSON.parse(JSON.stringify(data))._body);
            this.modalNovoClose();
          }
          catch(err) {
            alert("Login j utilizado. Tente outro.")
          }

        },
        error => console.log(error)
        ); 
      else 
        this.userService.getEmpresaByEmpresaName(localStorage.getItem("currentEmpresa")).subscribe(
          data => {this.newUser.empresa = JSON.parse(JSON.parse(JSON.stringify(data))._body);
            this.registerService.sendUser(this.newUser)
            .subscribe(
              data => {
                this.newUser = new User();
                let userReturn: User;

                try {
                  userReturn = JSON.parse(JSON.parse(JSON.stringify(data))._body)
                  this.modalNovoClose();
                }
                catch(err) {
                  alert("Login j utilizado. Tente outro.")
                }

              },
              error => console.log(error)
              ); 

          }, 
          error => console.log(error)
          );

    }
    
  }

  verificarSenhas(pass:string){
    if (pass.length < 6) {
        alert("A senha deve conter pelo menos 6 caracteres.");
        return false;
    } else if (pass.length > 20) {
        alert("A senha no pode ter mais do que 20 caracteres");
        return false;
    } else if (pass.search(/\d/) == -1) {
        alert("A senha deve conter pelo menos um nmero.");
        return false;
    } else if (pass.search(/[a-zA-Z]/) == -1) {
        alert("A senha deve conter pelo menos uma letra");
        return false;
    } else if (pass.search(/[`~!@#$%^&*()_|+\-=?;:'",.<>\{\}\[\]\\\/]/gi) != -1) {
        alert("A senha no pode conter caracteres especiais");
        return false;
    }
    else if(!(this.passVerify === pass)){
            alert("As duas senhas devem coincidir!");
          return false;
        }
        else return true;
  }

  verificarLogin(userName:string){
    if (userName.length < 6) {
        alert("O login deve conter pelo menos 6 caracteres.");
        return false;
    } else if (userName.length > 20) {
        alert("O login no pode ter mais do que 20 caracteres");
        return false;
    } else if (userName.search(/[a-zA-Z]/) == -1) {
        alert("O login deve conter pelo menos uma letra");
        return false;
    } else if (userName.search(/[`~!@#$%^&*()_|+\-=?;:'",.<>\{\}\[\]\\\/]/gi) != -1) {
        alert("O login no pode conter caracteres especiais");
        return false;
    }else return true;
  }


  salvarEdicao(){
    let userReturn: User;
    this.userService.updateUser(this.userEdit)
    .subscribe(
      data => {
        userReturn = JSON.parse(JSON.parse(JSON.stringify(data))._body)
        this.userEdit = new User();
        this.fecharModalEditar();

      },
      error => console.log(error)
      );

  }

  salvarEmpresa() {
    this.userService.sendEmpresa(this.newEmpresa)
    .subscribe(
      data => {
        this.newEmpresa = new Empresa();
        let empresaReturn: Empresa;


        try {
          empresaReturn = JSON.parse(JSON.parse(JSON.stringify(data))._body)
          this.refreshEmpresas();
          this.cancelarEmpresaRef.nativeElement.click();

        }
        catch(err) {
          alert("Nome Fantasia j utilizado. Tente outro.")
        }

      },
      error => console.log(error)
      );
  }

  openModalEdit(user: User){
    this.userEdit = user;
    this.modalEditar.show();
  }

  fecharModalEditar(){
    this.findUsers();
    this.modalEditar.hide();
  }

  resetarSenha(user: User){

    this.userService.resetSenha(user).subscribe(
      data => {
        let senha: NovaSenhaCampos = JSON.parse(JSON.parse(JSON.stringify(data))._body);
        alert("A senha do Usurio "+user.userName+" foi alterada para "+senha.newPassword+".");
      },
      error => console.log(error)
      );

  }




}



