import { Component, ViewContainerRef } from '@angular/core';
import { Overlay } from 'angular2-modal';
import { Modal } from 'angular2-modal/plugins/bootstrap';
import {PdfService} from '../services/pdf.service';

@Component({
  selector: 'modal',
  template: `<a class="ui button" (click)="onClick()">
                        <i class="trash icon" ></i> 
                    </a>`
})

export class MyModal{
  constructor(overlay: Overlay, vcRef: ViewContainerRef, public modal: Modal, private pdfService: PdfService) {
    overlay.defaultViewContainer = vcRef;
  }

  onClick(pdf) {
    this.modal.confirm()
    .size('sm')
    .isBlocking(true)
    .keyboard(27)
    .title('Confirma excluso da Inspeo?')
    .open().then(result => { if(result) {this.deletePdf(pdf)} });
  }

  deletePdf(pdf){
      if(this.onClick(pdf)){
      this.pdfService.deletePdf(pdf).subscribe();
    }

    
  }
}


