import {Injectable} from '@angular/core';
import {Http, Headers} from '@angular/http';

import {HostService}  from '../services/host.service';

import {User} from '../models/user';
import {Empresa} from '../models/empresa';
import {NovaSenhaCampos} from '../models/novaSenhaCampos';


@Injectable()
export class UserService {
  users: User[];
  user: User;

  constructor (private http: Http, private hostService: HostService) {
    this.getUserByName(localStorage.getItem("currentUserName")).subscribe(
      user => {
        this.user = JSON.parse(JSON.parse(JSON.stringify(user))._body);
      },
      );
  }

  resetSenha(user:User) {
    let url = this.hostService.host+"rest/super/user/resetSenha";
    let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(url, JSON.stringify(user), {headers: headers});
  }

  updateSenha(novaSenhaCampos:NovaSenhaCampos) {
    let url = this.hostService.host+"rest/simple/user/updateSenha";
    let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(url, JSON.stringify(novaSenhaCampos), {headers: headers});
  }

  updateUser(user:User) {
    let url = this.hostService.host+"rest/admin/user/update";
    let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(url, JSON.stringify(user), {headers: headers});
  }

  checkLogin(){
    let tokenUrl = this.hostService.host+"rest/user/checkLogin";
    let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(tokenUrl, '', {headers: headers});
  }

  checkAdmin(username: string){
    let tokenUrl = this.hostService.host+"rest/user/checkAdmin";
    let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(tokenUrl, username, {headers: headers});
  }

  getUserByName(username: string) {
    let tokenUrl = this.hostService.host+"rest/simple/user/userName";
    let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(tokenUrl, username, {headers: headers});
  }

  getUsersByUserName(username: string) {
    let tokenUrl = this.hostService.host+"rest/super/users/userName";
    let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(tokenUrl, username, {headers: headers});
  }

  getUsersByUserNameAndEmpresaName(usernameandempresaname: string) {
    let tokenUrl = this.hostService.host+"rest/admin/users/userNameAndEmpresaName";
    let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(tokenUrl, usernameandempresaname, {headers: headers});
  }

  getAllUsers() {
    let tokenUrl = this.hostService.host+"rest/super/user/users";
    let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(tokenUrl, '', {headers: headers});
  }

  getUsersByEmpresa(empresa: string) {
    let tokenUrl = this.hostService.host+"rest/admin/user/usersByEmpresa";
    let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(tokenUrl,  JSON.stringify(empresa) ,{headers: headers});
  }

  getAllEmpresas() {
    let tokenUrl = this.hostService.host+"rest/super/user/empresa/empresas";
    let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(tokenUrl, '', {headers: headers});
  }

  getEmpresaByEmpresaName(empresaName:string) {
    let url = this.hostService.host+"rest/simple/users/empresa/empresaFantasyName";
    let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(url, JSON.stringify(empresaName), {headers: headers});
  }

  sendEmpresa(empresa:Empresa) {
    let url = this.hostService.host+"rest/super/user/empresa/save";
    let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(url, JSON.stringify(empresa), {headers: headers});
  }




}






