import {Injectable} from "@angular/core";
import {Http, Headers} from '@angular/http';
import {Observable}     from 'rxjs/Observable';
import {User}    from '../models/user';
import {UserService}  from '../services/user.service';
import {HostService}  from '../services/host.service';



@Injectable()
export class LoginService {
  token: string;
  user: User;
  userLogado: User = new User();
  private isAdminLocal: boolean;
  private isSuperAdminLocal: boolean;

  constructor (private http: Http, private userService: UserService, private hostService: HostService) {}

  sendCredential(model) {
    let tokenUrl1 = this.hostService.host+"login";
    let headers1 = new Headers({'Content-Type': 'application/json'});
    return this.http.post(tokenUrl1, JSON.stringify(model), {headers: headers1});
  }

  sendToken(token) {
    /*let tokenUrl2 = this.hostService.host+"rest/user/users";*/
    let tokenUrl2 = this.hostService.host+"rest/simple/user/token";
    /*console.log('Bearer '+token);*/

    let getHeaders = new Headers({'Authorization':'Bearer '+token});

    return this.http.get(tokenUrl2, {headers: getHeaders})
  }

  logout() {
    localStorage.setItem("token","");
    localStorage.setItem("currentUserName", '');
    localStorage.setItem("currentIsAdmin", '');
  }

  checkLogin() {
    if (localStorage.getItem("currentUserName")!=null && localStorage.getItem("currentUserName")!='' && localStorage.getItem("token")!=null && localStorage.getItem("token")!='') {
/*      console.log(localStorage.getItem("currentUserName"));
console.log(localStorage.getItem("token"));*/
return true;
} else {
  return false;
}
}

checkAdmin() {
  this.userService.getUserByName(localStorage.getItem("currentUserName")).subscribe(
    user => {
      this.userLogado = JSON.parse(JSON.parse(JSON.stringify(user))._body);
    }, error => console.log(error)
    );
    this.isAdminLocal = this.userLogado.isAdmin;
    return this.isAdminLocal;  

}


checkSuperAdmin() {
  this.userService.getUserByName(localStorage.getItem("currentUserName")).subscribe(
    user => {
      this.userLogado = JSON.parse(JSON.parse(JSON.stringify(user))._body);
      this.userLogado.isSuperAdmin;  
      
    }, error => console.log(error)
    );
    this.isSuperAdminLocal = this.userLogado.isSuperAdmin;
    return this.isSuperAdminLocal;  

  
}


getUserOnLoadAllPagesSecurity(){
  this.userService.getUserByName(localStorage.getItem("currentUserName")).subscribe(
    user => {
      this.userLogado = JSON.parse(JSON.parse(JSON.stringify(user))._body);
      localStorage.setItem("currentUserName", this.userLogado.userName);
      localStorage.setItem("isAdmin", this.userLogado.isAdmin? "true": "false");
      localStorage.setItem("isSuperAdmin", this.userLogado.isSuperAdmin? "true": "false");
    },
    );  
}


/*checkAdminLocal(){
  if (localStorage.getItem("currentUserName")!=null && localStorage.getItem("currentUserName")!='' 
    && localStorage.getItem("token")!=null && localStorage.getItem("token")!=''
    && localStorage.getItem("currentIsAdmin")!=null && localStorage.getItem("currentIsAdmin")!='') {
return true;
} else {
  return false;
}

}*/


}



