import { Component, ElementRef, Renderer, OnInit  } from '@angular/core';
import {BrowserModule, DomSanitizer, SafeResourceUrl} from '@angular/platform-browser';
import {Pdf} from '../models/pdf';
import {PdfService} from '../services/pdf.service';
import {UserService} from '../services/user.service';
import {LoginService} from '../services/login.service';
import {PdfUserFilter} from '../models/filters/pdf-user-filter';

import {User} from '../models/user';
import {Router} from '@angular/router';
import { ViewChild } from "@angular/core/src/metadata/di";
import {HostService}  from '../services/host.service';

import {AuthGuardSuperAdmin} from '../guards/auth.guard.superadmin';
import {AuthGuardAdmin} from '../guards/auth.guard.admin';

import {ModalComponent}  from '../components/modal-pdf.component';



@Component({
  selector: 'pdf-admin',
  templateUrl: './pdf-admin.component.html'
})

export class PdfAdmin implements OnInit{

  @ViewChild("nao") naodelref:ElementRef;
  @ViewChild("modal2") modal:ModalComponent;


  pdfSrcBase = this.hostService.host+'/pdf/download'

  page:number = 1;
  pageurl:SafeResourceUrl;
  pageurl2:SafeResourceUrl;

  urlPdfs:SafeResourceUrl[];

  pdfs: Pdf[];
  selectedPdf: Pdf;
  zoom=0.5;

  pdfUserFilter: PdfUserFilter = new PdfUserFilter();

  users: User[];

  subtipoRender: boolean = false;
  refreshAll: boolean = false;

  aindaNaoBuscado: boolean;

  private user: User;

  constructor(private domSanitizer:DomSanitizer, private pdfService: PdfService,  private router: Router, 
    private userService: UserService, private rd: Renderer, private loginService: LoginService, 
    private hostService: HostService, private authGuardSuperAdmin: AuthGuardSuperAdmin, private authGuardAdmin: AuthGuardAdmin) {

    this.aindaNaoBuscado =true;

  }

  ngOnInit(){
    if(this.authGuardSuperAdmin.canActivate()){
      this.userService.getAllUsers().subscribe(
        users => {
          this.users = JSON.parse(JSON.parse(JSON.stringify(users))._body);
        }
        ,
        error => console.log(error) 
        );

    }

    else {
      this.userService.getUsersByEmpresa(this.loginService.userLogado.empresa.empresaFantasyName).subscribe(
        users => {
          this.users = JSON.parse(JSON.parse(JSON.stringify(users))._body);
        }
        ,
        error => console.log(error) 
        );

    }
  }


  findPdfs(userSelected: string){
    this.userService.getUserByName
    (userSelected).subscribe(
      user => {
        this.user = JSON.parse(JSON.parse(JSON.stringify(user))._body);
        this.pdfUserFilter.user = this.user;
        this.pdfService.getPdfsByFilter(this.pdfUserFilter).subscribe(
          pdf => {this.pdfs = JSON.parse(JSON.parse(JSON.stringify(pdf))._body);
            this.refreshAll = false;
            this.aindaNaoBuscado =false;
          },
          error => console.log(error)
          );
      },
      error => console.log(error)
      );

  }

  filtersSetted(){
    if(this.pdfUserFilter.ano  && this.pdfUserFilter.tipo 
      && this.pdfUserFilter.user.userName){

      if(this.pdfUserFilter.tipo == "1"){
        if(this.pdfUserFilter.subTipo){
          return true;
        } 
        else { 
          return false;
        }
      } 
      else { 
        return true;
      } 

    } 
    else { 
      if(!this.pdfUserFilter.ano  && !this.pdfUserFilter.tipo  
        && this.pdfUserFilter.user.userName){
        return true;
    } else {
      return false;
    }

  }
}

sanitize(fileName) {
  return this.domSanitizer.bypassSecurityTrustResourceUrl(this.pdfSrcBase+"/"+fileName+"/"+localStorage.getItem("token")+"/"+localStorage.getItem("currentUserName"));
}

sanitizeTeste(fileName) {
  return this.domSanitizer.bypassSecurityTrustResourceUrl(fileName);
}

sanitizeTeste2(fileName) {
  return this.domSanitizer.bypassSecurityTrustResourceUrl(this.pdfSrcBase+"/"+localStorage.getItem("currentUserName"));
}


getPdf(pdf: Pdf){
  this.userService.getUserByName(localStorage.getItem("currentUserName")).subscribe(
    user => {
      this.user = JSON.parse(JSON.parse(JSON.stringify(user))._body);
      console.log(this.user);
      this.pdfService.downloadPdfPost(pdf).subscribe(
        (res) => {
          /*let blob: Blob = new Blob([res.arrayBuffer()], {type: 'application/pdf'});*/
          var pdfResponse = new Blob([res.blob()], { type: 'application/pdf' });
          console.log(pdfResponse.size + " bytes file downloaded. File type: ", pdfResponse.type);                

          var fileURL = URL.createObjectURL(pdfResponse);
          console.log("Url: " + fileURL);
          document.getElementById("pdfModal").setAttribute("src", fileURL);
          this.modal.show();


          /* window.open(fileURL);*/
        },
        error => console.log(error)
        );
    },
    error => console.log(error)
    );



}



deletePdf(pdf){
  this.pdfService.deletePdf(pdf).subscribe();
  this.naodelref.nativeElement.click();
  this.refresh();

}

existePdf(){
  if(this.pdfs == null || this.pdfs.length == 0){
    return true;
  } else {
    return false;
  }
}


existePdfAndAindaNaoBuscado(){
  if(this.existePdf() && this.aindaNaoBuscado)
    return true;
  else return false;
}

existePdfAndNotAindaNaoBuscado(){
  if(this.existePdf() && !this.aindaNaoBuscado)
    return true;
  else return false;
}


refresh(){

  if(this.authGuardSuperAdmin.canActivate()){
      this.pdfService.getAllPdfs().subscribe(
        data => this.pdfs = JSON.parse(JSON.parse(JSON.stringify(data))._body),
        error => console.log(error)
        );
    }

    else  if(this.authGuardAdmin.canActivate()){
      this.pdfService.getPdfsByEmpresaFantasyName(this.loginService.userLogado.empresa.empresaFantasyName).subscribe(
        data => {
          this.pdfs = JSON.parse(JSON.parse(JSON.stringify(data))._body);
        }
        ,
        error => console.log(error) 
        );

    }
}


onChangeDrop(teste){
  if(this.pdfUserFilter.tipo == "1"){
    this.subtipoRender = true;
  } else {
    this.pdfUserFilter.subTipo = null;
    this.subtipoRender = false;

  }


}








}


