  import {Component} from '@angular/core';
  import {Observable}  from 'rxjs/Observable';
  import {LoginService} from '../services/login.service';
  import {UserService} from '../services/user.service';
  import {User} from '../models/user';

  @Component({
    selector: 'login',
    templateUrl: './login.component.html'
  })
  export class Login {
    private model = {'username':'', 'password':'', 'isAdmin':''};
    private currentUserName;
    private currentIsAdmin;
    private user: User;

    constructor (private loginService: LoginService, private userService: UserService){
      this.currentUserName=localStorage.getItem("currentUserName");
      this.currentIsAdmin=localStorage.getItem("currentIsAdmin");
    }

    onSubmit() {
      this.loginService.sendCredential(this.model).subscribe(
        data => {
          localStorage.setItem("token", JSON.parse(JSON.stringify(data))._body);
          this.loginService.sendToken(localStorage.getItem("token")).subscribe(
            data => {
              this.currentUserName=this.model.username;
              this.currentIsAdmin=this.model.isAdmin;
              localStorage.setItem("currentUserName", this.model.username);

              this.userService.getUserByName(localStorage.getItem("currentUserName")).subscribe(
                user => {
                  this.user = JSON.parse(JSON.parse(JSON.stringify(user))._body);
                  this.loginService.userLogado = this.user;

                  let tostring: string = "";
                  if(this.user.isAdmin){
                    tostring = "true";
                  }
                  
                  localStorage.setItem("currentIsAdmin", tostring);
                  localStorage.setItem("currentEmpresa", this.user.empresa.empresaFantasyName.toString());
                },
                error => console.log(error)
                );
              this.model.username='';
              this.model.password='';
              this.model.isAdmin='';
            },
            error => { 
              console.log(error);
              alert("Login ou senha invlidos");
            }
            );
        },

         error => { 
              console.log(error);
              alert("Login ou senha invlidos");
            }

        );

    }


  }



